.search-wrapper{background-color:#fff;max-width:240px;display:flex;align-items:center;padding:3px;margin:auto;border:2px solid #fff;border-radius:10px;transition:500ms ease-in-out}.search-wrapper:focus-within{border:2px solid wheat}.search-wrapper .search-icon{margin-right:5px;padding:1px}.search-wrapper input{border:none;padding:1px}

.profile{min-height:400px;border-radius:46px;background:#4f3535;box-shadow:8px 8px 13px #392626, -8px -8px 13px #654444}.profile .profile__pic{width:125px;height:100px;border-radius:50%}

.social{display:flex;justify-content:space-between}.social .social__link{color:white}.social .social__link i{font-size:20px;margin:0 10px}

.container{margin:10px auto;height:85vh;border-radius:2%;background-color:#4f3535}.container h1{color:wheat}

body{margin:0;padding:0;box-sizing:border-box;background-color:#0f0e0e}

